<template>
  <div id="app">
    <h2>
      <span v-on:click="this.previousWeek">⬅</span>
      Week of {{selectedWeekFormatted}}
      <span v-on:click="this.nextWeek">➡</span>
    </h2>
    <Chart v-bind="{ expenses, selectedWeek }" />
  </div>
</template>

<script>
import * as d3 from "d3";
import _ from "lodash";
import Chart from "./components/Chart";
import expensesData from "./data/expenses.json";

export default {
  name: "App",
  components: {
    Chart
  },
  data() {
    return {
      expenses: [],
      selectedWeek: null,
      selectedWeekFormatted: null
    };
  },
  mounted() {
    this.expenses = _.chain(expensesData)
      .filter(d => d.Amount < 0)
      .map(d => {
        return {
          amount: -d.Amount,
          name: d.Description,
          date: new Date(d["Trans Date"])
        };
      })
      .value();
    // console.log(this.expenses);

    // default selected week will be the most recent week
    this.selectedWeek = d3.max(this.expenses, exp =>
      d3.timeWeek.floor(exp.date)
    );
    this.selectedWeekFormatted = d3.timeFormat("%B %d, %Y")(this.selectedWeek);
  },
  methods: {
    previousWeek: function() {
      console.log(d3.timeWeek.offset(this.selectedWeek, -1));
      this.selectedWeek = d3.timeWeek.offset(this.selectedWeek, -1);
      this.selectedWeekFormatted = d3.timeFormat("%B %d, %Y")(
        this.selectedWeek
      );
    },
    nextWeek: function() {
      console.log(d3.timeWeek.offset(this.selectedWeek, 1));
      this.selectedWeek = d3.timeWeek.offset(this.selectedWeek, 1);
      this.selectedWeekFormatted = d3.timeFormat("%B %d, %Y")(
        this.selectedWeek
      );
    }
  }
};
</script>

<style>
* {
  font-family: Arial, Helvetica, sans-serif;
}
</style>
