<template>
  <div>
    <svg :width="width" :height="height*2">
      <g ref="test" />
    </svg>
  </div>
</template>

<script>
import _ from "lodash";
import chroma from "chroma-js";
import * as d3 from "d3";

var width = 900;
var height = 500;
var radius = 8;
var margin = { top: 20, right: 40, bottom: 20, left: 40 };

var simulation = d3
  .forceSimulation()
  // .force("center", d3.forceCenter(width / 2, height / 2))
  //   .force('charge', d3.forceManyBody())
  .alphaDecay(0.005)
  .velocityDecay(0.3)
  .force("collide", d3.forceCollide(radius))
  .force(
    "x",
    d3.forceX(d => d.focusX)
  )
  .force(
    "y",
    d3.forceY(d => d.focusY)
  );

var daysOfWeek = [
  [0, "S"],
  [1, "M"],
  [2, "T"],
  [3, "W"],
  [4, "Th"],
  [5, "F"],
  [6, "S"]
];

var xScale = d3
  .scaleBand()
  // .domain([0, 1, 2, 3, 4, 5, 6])
  .domain(_.map(daysOfWeek, 0)) //0 for get 1st element
  .range([margin.left, width - margin.right]);
var yScale = d3.scaleLinear().range([height - margin.bottom - 30, margin.top]);
var colorScale = chroma.scale(["#53cf8d", "#f7d283", "#e85151"]);
var amountScale = d3.scaleLog();
var expensesModified = [];

export default {
  name: "chart",
  props: ["expenses", "selectedWeek"],
  data() {
    return {
      width,
      height
    };
  },
  mounted() {
    // this.test();
  },
  watch: {
    expenses: function() {
      this.calculateData();
      this.renderWeeks();
      this.renderDays();
      this.renderCircles();
    },
    selectedWeek: function() {
      this.calculateData();
    }
  },
  methods: {
    calculateData: function() {
      console.log(this.selectedWeek);
      //Object.assign to add stuff to original
      //groupBy groups arrays by same week. Floor date to first date of week, i.e. Sunday. Returns an obj of arrays (named as Dates) of objs --> use "Date names" for sortBy
      //groupBy returns object, sortBy returns array, map returns array, array of array so flatten [[]] --> []
      //getDay() returns integer

      // var row = -1;
      var weeksExtent = d3.extent(this.expenses, d =>
        d3.timeWeek.floor(d.date)
      );
      yScale.domain(weeksExtent);

      // var selectedWeek = weeksExtent[1];
      var perAngle = Math.PI / 6;
      var selectedWeekRadius = (width - margin.left - margin.right) / 2;

      //interval.range vs scale.range

      var weeks = d3.timeWeek.range(
        weeksExtent[0],
        d3.timeWeek.offset(weeksExtent[1], 1)
      );

      this.weeks = _.map(weeks, week => {
        return {
          week,
          x: margin.left,
          y: yScale(week) + height + margin.top
        };
      });

      this.days = _.map(daysOfWeek, date => {
        // console.log(date);
        var [dayOfWeek, name] = date;
        var angle = Math.PI - perAngle * dayOfWeek;
        var x = selectedWeekRadius * Math.cos(angle) + width / 2;
        var y = selectedWeekRadius * Math.sin(angle) + margin.top;
        return {
          name,
          x,
          y
        };
      });

      expensesModified = _.chain(this.expenses)
        .groupBy(d =>
          //   console.log(d.date);
          d3.timeWeek.floor(d.date)
        )
        // .sortBy((expenses, week) => {
        //   //   console.log(expenses, week);
        //   new Date(week); // some issues here!
        // })
        .map((expenses, week) => {
          // row += 1;
          week = new Date(week);
          return _.map(expenses, exp => {
            var dayOfWeek = exp.date.getDay();
            var focusX = xScale(dayOfWeek) + margin.left;
            // focusY: row * 50
            var focusY = yScale(week) + height + margin.top;

            if (week.getTime() === this.selectedWeek.getTime()) {
              var angle = Math.PI - perAngle * dayOfWeek;

              focusX = selectedWeekRadius * Math.cos(angle) + width / 2;
              focusY = selectedWeekRadius * Math.sin(angle) + margin.top;
            }

            return Object.assign(exp, {
              focusX,
              focusY
            });
          });
        })
        .flatten()
        .value();
      // added focusX and focusY to the original expenses

      //   console.log(expensesModified);

      simulation.on("tick", this.forceTick);
      simulation.nodes(expensesModified).alpha(0.5);

      var amountExtent = d3.extent(this.expenses, d => d.amount);
      //   console.log(amountExtent);
      amountScale.domain(amountExtent);
    },
    renderCircles: function() {
      this.test = d3.select(this.$refs.test);

      //this.circles to make circles available to all methods
      this.circles = this.test
        .selectAll(".expense")
        .data(expensesModified, d => d.name);

      this.circles.exit().remove();

      this.circles = this.circles
        .enter()
        .append("circle")
        .classed("expense", true)
        .attr("fill-opacity", 0.25)
        .attr("stroke-width", 3)
        .merge(this.circles)
        .attr("r", radius)
        .attr("fill", d => colorScale(amountScale(d.amount)))
        .attr("stroke", d => colorScale(amountScale(d.amount)));
    },
    renderDays: function() {
      this.test = d3.select(this.$refs.test);

      var days = this.test
        .selectAll(".day")
        .data(this.days, d => d.name)
        .enter()
        .append("g")
        .classed("day", true) //add a class
        .attr("transform", d => `translate(${d.x},${d.y})`);

      var daysRadius = 50;
      var fontSize = 12;

      days
        .append("circle")
        // .attr("cx", d => d.x)
        //   .attr("cy", d => d.y)
        .attr("r", daysRadius)
        .attr("fill", "#ccc")
        .attr("opacity", 0.25);

      days
        .append("text")
        .attr("y", daysRadius + fontSize)
        .attr("text-anchor", "middle")
        .attr("dy", ".35em")
        .attr("fill", "#ccc")
        .attr("font-weight", 600)
        .text(d => d.name);
    },
    renderWeeks: function() {
      this.test = d3.select(this.$refs.test);
      var weeks = this.test
        .selectAll(".week")
        .data(this.weeks, d => d.name)
        .enter()
        .append("g")
        .classed("week", true)
        .attr("transform", d => `translate(${d.x},${d.y})`);

      var rectHeight = 10;

      weeks
        .append("rect")
        .attr("y", -rectHeight / 2)
        .attr("width", width - margin.left - margin.right)
        .attr("height", rectHeight)
        .attr("fill", "#ccc")
        .attr("opacity", 0.25);

      var weekFormat = d3.timeFormat("%m/%d");

      weeks
        .append("text")
        .attr("text-anchor", "end")
        .attr("dy", ".35em")
        .text(d => weekFormat(d.week));
    },
    forceTick: function() {
      //   console.log(this.circles.datum().x, this.circles.datum().y);
      this.circles.attr("cx", d => d.x).attr("cy", d => d.y);
    }
  }
};
</script>

<style></style>
